











import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class Loadmore extends Vue {
  @Prop()
  isFetching:any
  @Prop()
  isMore:any
  @Prop()
  options:any

  addData() {
    let { currentPage } = this.options;
    this.$emit('addData', { currentPage: ++currentPage }, true);
  }
}
