






























import { Component, Prop, Vue } from 'vue-property-decorator';
import tiny from '@/assets/images/tiny.gif';

@Component
export default class Tags extends Vue {
  @Prop()
  tagList:any
  @Prop()
  options:any
  @Prop()
  isFetching:any

  loadingImg:any = tiny;

  changeSort(sortName:any) {
    this.$emit('change', {
      currentPage: 1,
      sortName,
      tagId: '',
    });
  }

  changeTag(tagId:any) {
    this.$emit('change', { currentPage: 1, sortName: '', tagId });
  }
}
